export default function (classes) {

  var classMap = new Map();
  var instanceMap = new Map();

  for(var key in classes){
      instanceMap.set(key, classes[key]);
  }
  
  instanceMap.forEach(function (eachInstance, key) {
      var injectInstances = [];
      var instanceClass = classMap.get(key);
      eachInstance['_injectDecorator_injectClazz'] && eachInstance['_injectDecorator_injectClazz'].forEach(function (injectClazzName) {
          if (!instanceMap.get(injectClazzName)) {
              throw 'injectName: ' + eachInstance[injectClazzName] + ' not found!';
          }
          eachInstance[injectClazzName] = instanceMap.get(injectClazzName);
      });
      delete eachInstance['_injectDecorator_injectClazz'];
  });
  
  return instanceMap;
};